body {
  font-family: $font-basic;
  font-size: 17px;
  font-weight: 400;
  color: $text;

  margin: 0;
  padding: 0;
}

html,
body {
  min-width: 320px;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

a {
  text-decoration: none;
  color: #141517;
}

img {
  max-width: 100%;
  height: auto;
}

.lock {
  overflow: hidden;
}

.hidden {
  display: none;
}

.validation-error:invalid {
  box-shadow: none;
  border-color: $input-error;
}

.validation-success:valid {
  box-shadow: none;
  border-color: $text;
}
